html {
    box-sizing: border-box;
    width: 100vw;
    overflow-x: hidden;
    }
    
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
    color: #212121;
    background-color: white;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  

  
.statistics {
    font-family: "Lucida Sans Unicode", "Lucida Grande", Sans-Serif;
    font-size: 24px;
    max-width: 60%;
    width: 60%;
    text-align: center;
    margin: 180px auto;
    border: 2px solid rgb(199, 204, 204);
  }
  
  h2 {
    font-weight: bold;
    color: rgb(94, 91, 91);
    border: 2px solid white;
    text-transform: uppercase;
    padding: 50px 40px;
    border-bottom: 2px solid rgb(199, 204, 204);
  }
  
  
  .stat-list {
     display: flex;
     list-style: none;
     
  } 
  .item {
    width: 100%;
  }
  