html {
    box-sizing: border-box;
    width: 100vw;
    overflow-x: hidden;
    }
    
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
    color: #212121;
    background-color: white;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  

  

.profile {
    font-family: "Lucida Sans Unicode", "Lucida Grande", Sans-Serif;
    font-size: 20px;
    max-width: 26%;
    width: 26%;
    text-align: center;
    margin: 120px auto;
    border: 2px solid rgb(199, 204, 204);
    background: rgb(235, 233, 233); 
  }
  
  .stats{
    display: flex;
    list-style: none;
    justify-content: space-around;
    text-align: center ;
  }
   
  .description {
  background: white;
  } 
  
  .avatar {
    margin-top: 34px; 
    border-radius: 50%; 
  }
  
  .tag  {
    color: grey;
  }
  
  .location {
    color: grey;
  }
  
  .label {
  color: grey;
  } 
  
  .quantity {
    display: block;
    font-weight: bold;
  }