html {
    box-sizing: border-box;
    width: 100vw;
    overflow-x: hidden;
    }
    
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
    color: #212121;
    background-color: white;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
.friend-list{
    font-family: "Lucida Sans Unicode", "Lucida Grande", Sans-Serif;
    font-size: 24px;
    background: white;
    max-width: 30%;
    width: 30%;
    border-collapse: collapse;
    border-spacing: 10px;
    text-align: center;
    margin: 10px auto;
    /* display: flex;
    flex-direction: column;
    justify-content: space-between; */
   
  }
  
  
  .item {
     border: 1px solid rgb(199, 204, 204); 
  }
  
  
  .avatar {
    position: relative; 
    width: 110px; 
    height: 110px;
    margin-right: 180px; 
    margin-top: 5px;
  }
  .name {
    position: absolute;
    margin: -80px auto; 
    padding-left: 180px;
  }